import React, {useState} from 'react'
import './room.css'
import salaA from '../offer/sala2.jpg'

const RoomA = () => {
    const [isShown, setIsShown] = useState(true)
    const labels = ['Gitara 1:', 'Gitara 2:', 'Bas:', 'Perkusja:', 'Przody:', 'Monitory:', 'Mikser:', 'Mikrofony:']
    const label = labels.map((l) => 
    <ul><b>{l}</b></ul>
    )

    const salaAdesc = "Powierzchnia 25 metrów kwadratowych, klimatyzowana, ogrzewana"   
    const salaAequip = [
    'Laboga Beast 30W Plus + Paczka Laboga 212a',
    'Fender Champion 100 2x12',
    'Ashdown Mag EVO II 300 1x15',
    'Mapex Armory Studioease Fast',
    'bB Technologies B-Hype 10',
    'the box MA100 (2 na salkę)',
    'Soundcraft EPM 6',
    'Shure SM 58'
    ]
    const salaAeq = salaAequip.map((ea) => 
    <ul>{ea}</ul>
)


    return (
        <div className="room">
            <div className="room-content">
                {isShown &&
                <div className="room-title">Sala A</div>}
                <div className="room-left">
                    <img 
                    onClick={() => setIsShown(!isShown)} 
                    src={salaA}/>
                </div>
                {isShown && (
                <div className="room-right sala-a">
                    <div className="room-right-up">{salaAdesc}</div>
                    <div className="room-right-down">
                        <div className="labels">{label}</div>
                        <div className="items">{salaAeq}</div>   
                    </div>
                </div>)}
            </div>
            
        </div>
    )
}

export default RoomA
