import React, {useState} from 'react'
import './room.css'
import salaC from '../offer/studio2.jpg'

const RoomC = () => {
    const [isShown, setIsShown] = useState(true)
    const labels = ['Mikrofony:', 'Monitory:', 'Mixer:', 'Wzmacniacz słuchawkowy:', 'Odsłuchy douszne:', 'Plug iny:', 'DAW:']
    const label = labels.map((l) => 
    <ul><b>{l}</b></ul>
    )
    const salaCdesc = "Powierzchnia 35 metrów kwadratowych, klimatyzowana, ogrzewana"   
    const salaCequip = [
    'Shure, AKG, Audio Technika, Rode',
    'Yamaha HS8',
    'Allen and heath SQ5',
    'Behringer Powerplay HA6000',
    'Behringer powerplay P1',
    'Waves Horizon',
    'Reaper',
    ]
    const salaCeq = salaCequip.map((eb) => 
    <ul>{eb}</ul>
    )


    return (
        <div className="room">
            <div className="room-content">
                {isShown && 
                <div className="room-title">Studio nagrań</div>}
                <div className="room-left">
                    <img 
                    onClick={() => setIsShown(!isShown)}
                    src={salaC}/>
                </div>
                {isShown && (
                <div className="room-right sala-c">
                    <div className="room-right-up">{salaCdesc}</div>
                    <div className="room-right-down">
                        <div className="labels">{label}</div>
                        <div className="items">{salaCeq}</div>   
                    </div>
                </div>)}
            </div>
            
        </div>
    )
}

export default RoomC