import React, {useState} from 'react'
import Navbar from '../navbar/Navbar'
import "./header.css"
import { Link } from 'react-scroll'


const Header = () => {
    return (
    <div className="header" id="head">
        <Navbar 
        />
        <div className="landing-main">
          <div className="landing-title">Witamy na stronie sal prób oraz studia nagrań <b>Próbówka Music House Wrocław</b>.</div>
          <div className="landing-content">
            <div className="landing-title-second">
              Nasze dwie sale prób i studio nagrań znajdują się we Wrocławiu na ulicy Robotniczej 1a. 
            </div>
            <br></br><br></br>
            <div>
              Każda nasza sala prób to miejsce stworzone przez muzyków dla muzyków. Znamy wasze potrzeby. Miejsce jest klimatyzowane, ogrzewane, dostępne 24h na dobę, monitorowane, wyposażone w sprawdzony i zawsze działający sprzęt. Sala prób dysponuje również dużą ilością bezpłatnych miejsc parkingowych oraz toaletą. Oferujemy także możliwość bezpłatnego przechowywania swoich wzmacniaczy. Ponadto wraz z salami prób połączone jest profesjonalne studio nagrań. 
              U nas bez problemu zarejestrujesz swoją próbę, nagrasz swój występ czy utrwalisz materiał na planowaną płytę.
            </div>
            <br></br><br></br>
            <div>
              Sala prób, sala muzyczna, salka prób, studio nagrań, studio muzyczne, studio nagraniowe - jakkolwiek tego nie nazwiesz - znajdziesz to w <b>Próbówce</b>.
            </div>
            <Link to="booking" spy={true} smooth={true}><div className="landing-booking-button">REZERWACJA</div></Link>
          </div>
        </div>
        
          
        
    </div>
    )
}

export default Header
