import React, {useState} from 'react'
import './room.css'
import salaB from '../offer/studio3.jpg'

const RoomB = () => {
    const [isShown, setIsShown] = useState(true)
    const labels = ['Gitara 1:', 'Gitara 2:', 'Bas:', 'Perkusja:', 'Przody:', 'Monitory:', 'Mikser:', 'Mikrofony:']
    const label = labels.map((l) => 
    <ul><b>{l}</b></ul>
    )
    const salaBdesc = "Powierzchnia 35 metrów kwadratowych, klimatyzowana, ogrzewana"   
    const salaBequip = [
    'Laboga Beast 30W Plus + Paczka Laboga 212a',
    'Fender Champion 100 2x12',
    'Ashdown Mag EVO II 300 2x12',
    'Mapex Pro M',
    'bB Technologies B-Hype 10',
    'the box MA100 (2 na salkę)',
    'Soundcraft EPM 6',
    'Shure SM 58'
    ]
    const salaBeq = salaBequip.map((eb) => 
    <ul>{eb}</ul>
    )


    return (
        <div className="room">
            <div className="room-content">
                {isShown && 
                <div className="room-title">Sala B</div>}
                <div className="room-left">
                    <img 
                    onClick={() => setIsShown(!isShown)}
                    src={salaB}/>
                </div>
                {isShown && (
                <div className="room-right sala-b">
                    <div className="room-right-up">{salaBdesc}</div>
                    <div className="room-right-down">
                        <div className="labels">{label}</div>
                        <div className="items">{salaBeq}</div>   
                    </div>
                </div>)}
            </div>
            
        </div>
    )
}

export default RoomB